<!--记录膳食数据-->
<template>
    <s-header :name="'记录数据'"></s-header>

    <van-search :disabled="true"  @click="findFood" v-model="searchvalue" placeholder="请输入搜索关键词" class="van-search__content"/>
    <div class="div_title">
        <span>常见食物</span>
    </div>
<!--常见食物列表-->
    <div class="div_list">
        <div  class="div_list_item" v-for="(item,index) in datas" :key="index" @click="()=>onClickitem(item)">
            <div style="display: flex;align-items: center">
                <img :src="item['url']" class="img_item_left"/>
                <div style="display: flex;flex-direction: column;margin-left: 7px">
                    <span class="span_item_title">{{item.name}}</span>
                    <span class="span_item_des">{{item['nums'] + item.unit}}</span>
                </div>
            </div>
            <span>{{ item['heat'] }} {{ item['heatUnit']}}</span>
        </div>
    </div>
    <!-- 手动录入按钮 -->
    <van-button @click="onAddDefineFood" class="van-button--primary" type="primary" loading-text="加载中..." block>
      +不在列表，手写输入
    </van-button>
    <!--选择食物所属加餐弹出框-->
    <van-popup v-model:show="show_picker"  position="bottom">
        <van-picker  :columns="columns" @confirm="onConfirmPicker" @cancel="onCancelPicker"
                  />
    </van-popup>
    <!--选择食物弹出框-->
    <van-popup v-model:show="show" :teleport="myContainer" position="bottom">
        <div class="div_pop_content" :style="{ backgroundColor:'#fff'}">
            <div class="div_pop_content_title" @click="onShowPicker">
                <span>{{ pickFoodTitle.lable1 }}&nbsp;&nbsp;{{ pickFoodTitle.lable2 }}</span>
                <img src="@/assets/arrow_lower.png" style="margin-left: 10px"/>
            </div>
          <!--选中食物展示-->
            <div class="div_list_item" :style="{ borderTop: 0 }" :key="index">
                <div style="display: flex;align-items: center">
                    <img :src="selectitem['url']" class="img_item_left"/>
                    <div style="display: flex;flex-direction: column;margin-left: 7px">
                        <span class="span_item_title">{{ selectitem['name'] }}</span>
                    </div>
                </div>
            </div>
          <div class="layout_center">
            <span>{{ selectitem['unit'] }}</span>
          </div>
          <!--尺子-->
          <div :style="{ width: '100%', height:'70px'}">
            <Ruler :value="100" :min="10" :max="2000" :onChange="onRulerChange"></Ruler>
          </div>
            <div class="div_pop_content_unit" style="margin-top: 10px;padding: 10px 0px 20px 0px">
            </div>
          <!--确定和取消按钮-->
            <div  style="display: flex;background-color: #FFFFFF;">
                <van-button @click="onCancleClick" class="van-button--primary2" type="primary" block>取消</van-button>
                <van-button @click="onFoodAddClick" class="van-button--primary" type="primary" block>确定</van-button>
            </div>
        </div>
    </van-popup>
</template>

<script>
  import sHeader from '@/components/SimpleHeader'
  import Ruler from '../components/ruler/index.vue'
  import {reactive, onMounted, toRefs, toRaw} from 'vue'
  import moment from 'moment'
  import {useRoute, useRouter} from "vue-router";
  import { getFoodList, insertFoodRecord } from '@/service/food'
  import {dayTimeType, formatYYMMDD, formatYYMMDDHHMMSS} from "@/utils/util";
  import {Toast} from "vant";

  export default {
    components: {
      sHeader,
      Ruler
    },
    props: {
    },
    setup(props) {
      const router = useRouter()
      const route = useRoute()
      onMounted(
        () => {
          initData()
        }
      )

      // 初始化数据
      const initData = () => {
        // 初始化选择日期和加餐的
        initPickColumns()
        requestList()
      }

      const findFood = () => {
        const { query: { selectdate, typeEat } } = route
        router.push({ path: '/food-find', query:{ selectdate: selectdate, typeEat: typeEat } })
      }

      // 初始化选择日期和加餐的
      const initPickColumns = () => {
        const { query: { selectdate, typeEat } } = route

        const currntDate = new Date(selectdate)
        //  赋值弹出框
        const dateValues = []
        dateValues.push("今天")
        for (let i = 0; i < 6 ; i++) {
          let date = new Date(currntDate.setDate(currntDate.getDate() - 1))
          dateValues.push(moment(date). format('YYYY-MM-DD'))
        }
        state.columns[0].values = dateValues
        state.pickFoodTitle.lable1 =  new Date().toDateString() == new Date(selectdate).toDateString() ? "今天" :  moment(new Date(selectdate)). format('YYYY-MM-DD')
         const types =  toRaw(state.columns[1].values)
        console.log('初始化数据',selectdate)
        state.pickFoodTitle.lable2 = types[typeEat]
      }

      // 取消选择食物的弹出框
      const onCancleClick = () => {
        state.show = false
      }

      // 添加食物
      const onFoodAddClick = async () => {
        state.show = false
        const currentItem = state.selectitem
        const time = state.pickFoodTitle.lable1 === "今天"  ? new Date() : state.pickFoodTitle.lable1
        const res = await insertFoodRecord( formatYYMMDDHHMMSS(time), dayTimeType[`${state.pickFoodTitle.lable2}`],currentItem['name'],currentItem['heat'],
        currentItem['nums'], currentItem['unit'],currentItem['id'],currentItem['url'],formatYYMMDD(time))
        const { code, data } = res
        if(code === 200){
          Toast("添加成功")
        }else{
          Toast("添加失败")
        }
      }

      // 获取详情
      const requestList = async () => {
        const res = await getFoodList(state.selectdate, state.selectdate)
        const {code,data} = res

        state.datas = code === 200 ? res.data : []
        console.log("获取详情", state.datas)
      }


      // 添加自定义食物
      const onAddDefineFood = () => {
        const { query: { selectdate, typeEat } } = route
        router.push({ path: '/food-edit', query:{ selectdate: selectdate, typeEat: typeEat }})
      }

      /**
       * 刻度尺监听事件
       * @param value
       */
      const onRulerChange = (value) =>{
        state.NumValue = value
      }
      // 通过reactive 可以初始化一个可响应的数据，与Vue2.0中的Vue.observer很相似
      const state = reactive({
        pickFoodTitle: {
          lable1:"",
          lable2:""
        }, // 选择食物弹出框标题
        NumValue: 0, // 刻度
        searchvalue: '',
        show: false,
        selectitem: '',
        show_picker: false, // 选择加餐和日期
        datas: [],
        columns: [
          // 第一列
          {
            values: [],
            defaultIndex: 2,
          },
          // 第二列
          {
            values: ['早餐', '午餐', '晚餐', '加餐'],
            defaultIndex: 1,
          },
        ],
      })

      const onShowPicker = () => {
        state.show_picker = true;
      };
      const onClickitem = (item) => {
        state.selectitem = item;
        state.show = true;
      };
      const onCancelPicker = () => {
        state.show_picker = false;
      };
      const onConfirmPicker = (value, index) => {
        state.pickFoodTitle.lable1 = value[0]
        state.pickFoodTitle.lable2 = value[1]
        state.show_picker = false;
      };
      const myContainer = document.querySelector('.my-container');

      // 在Vue3.0中，所有的数据和方法都通过在setup 中 return 出去，然后在template中使用
      return {
        ...toRefs(state),
        onShowPicker,
        myContainer,
        onClickitem,
        onCancelPicker,
        onConfirmPicker,
        onRulerChange,
        onCancleClick,
        onFoodAddClick,
        onAddDefineFood,
        findFood
      }
    }
  }
</script>

